import React, { useState, useEffect } from "react";
import {Link} from 'react-router-dom';

const margin ={
  margin:"7% auto",
}

function buynow (){
alert("Thank you for choosing us")
}
const Cart = ({ cart, setCart,handleChange }) => {
  const [price, setPrice] = useState(0);

  const handleRemove = (id) => {
    const arr = cart.filter((item) => item.id !== id);
    setCart(arr);
    handlePrice();
  };

  const handlePrice = () => {
    let ans = 0;
    cart.map((item) => (ans += item.amount * item.price));
    setPrice(ans);
  };

  useEffect(() => {
    handlePrice();
  });

  return (<>
  <header>
        <div>
        <Link to="/"  className="logo"><i className="fas fa-utensils"></i>Stiller's</Link>
        <img className="logo-img" src="images/images-removebg-preview1.png" alt="" />
      </div>
        </header>
        <article className="cart--main">
      {cart.map((item) => (
        <div className="cart_box" key={item.id}>
          <div className="cart_img">
            <img src={item.img} alt="" />
            <p>{item.title}</p>
          </div>
          <div>
            <button onClick={() => handleChange(item, 1)}>+</button>
            <button>{item.amount}</button>
            <button onClick={() => handleChange(item, -1)}>-</button>
          </div>
          <div>
            <span>{item.price}</span>
            <button onClick={() => handleRemove(item.id)}>Remove</button>
          </div>
        </div>
      ))}
      <div className="total">
        <span>Total Price of your Cart</span>
        <span>Rs = {price}</span>
      </div>
      <a href="" className="butn" onClick={buynow}>buy now</a>
      {/* <Link><button className="butn">buy now</button></Link> */}
    </article>
    </>
  );
};

export default Cart;
